import express from 'express';
import { Joi, celebrate } from 'celebrate';
import bulkSaveUsers from '../controllers/users_post_bulk';
import { baseUrl } from '../baseurl';

const router = express.Router();

const userSchema = {
  body: {
    users: Joi.array()
      .items(
        Joi.object({
          userId: Joi.string().required(),
          mobileNumber: Joi.string().required(),
          email: Joi.string(),
          firstName: Joi.string().required(),
          lastName: Joi.string().required(),
        })
      )
      .required(),

    role: Joi.string().required(),
    type: Joi.string(),
    locationAccess: Joi.array().items(
      Joi.object({
        divisionType: Joi.string().required(),
        divisionLevel: Joi.number().required(),
        division: Joi.string().required(),
      })
    ),
  },
};

/**
 * @swagger
 * /users/bulk:
 *   post:
 *     tags:
 *       - Users
 *     description: It takes user details and adds a user.
 *     consumes:
 *       - application/json
 *     produces:
 *       - application/json
 *     parameters:
 *       - in: "body"
 *         name: "body"
 *         description: "Required user details"
 *         required: true
 *         schema:
 *           type: object
 *           properties:
 *              users:
 *                  type: array
 *                  items:
 *                    type: object
 *                    properties:
 *                        userId:
 *                            type: string
 *                        firstName:
 *                            type: string
 *                        lastName:
 *                            type: string
 *                        mobileNumber:
 *                            type: string
 *                        email:
 *                            type: string
 *              locationAccess:
 *                  type: array
 *                  items:
 *                    type: object
 *                    properties:
 *                      divisionType:
 *                        type: string
 *                      divisionLevel:
 *                        type: number
 *                      division:
 *                        type: string
 *              role:
 *                  type: string
 *              type:
 *                  type: string
 *     responses:
 *       200:
 *         description: A JSON object
 *         schema:
 *           type: object
 *           properties:
 *              userId:
 *                  type: string
 *              firstName:
 *                  type: string
 *              lastName:
 *                  type: string
 *              mobileNumber:
 *                  type: string
 *              email:
 *                  type: string
 *       400:
 *         description: Returns 400 if userId or password are invalid
 *         schema:
 *           type: object
 *           properties:
 *              statusCode:
 *                  type: number
 *                  example: 400
 *              error:
 *                  type: string
 *              message:
 *                  type: string
 */

router.post(`${baseUrl}/bulk`, celebrate(userSchema), bulkSaveUsers);

export default router;
